// QualityControlDashboard.jsx
import React, { useState, useEffect, useRef, useCallback } from 'react';
import {
  RefreshCw,
  AlertTriangle,
  CheckCircle,
  XCircle,
  Clock,
  DollarSign,
  TrendingUp,
  Activity
} from 'lucide-react';
import { toast } from 'sonner';
// âœ… P0 FIX: Use centralized API utility
import { apiGet, apiPost } from '../../utils/api';
import { logger } from '../../utils/logger';

const QualityControlDashboard = () => {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [timeRange, setTimeRange] = useState('24h');
  const [autoRefresh, setAutoRefresh] = useState(true);

  // âœ… FIX: Prevent duplicate concurrent requests
  const isFetchingRef = useRef(false);
  const mountedRef = useRef(true);

  const fetchData = useCallback(async () => {
    // Prevent duplicate requests
    if (isFetchingRef.current) return;
    isFetchingRef.current = true;

    try {
      // âœ… P0 FIX: Use centralized API utility
      const result = await apiGet(`/api/admin/quality-control?time_range=${timeRange}`, { showErrorToast: false });
      if (mountedRef.current) {
        setData(result);
        setLoading(false);
      }
    } catch (error) {
      logger.error('Failed to fetch quality data:', error);
      if (mountedRef.current) {
        toast.error('×©×’×™××” ×‘×˜×¢×™× ×ª × ×ª×•× ×™ ×‘×§×¨×ª ××™×›×•×ª');
        setLoading(false);
      }
    } finally {
      isFetchingRef.current = false;
    }
  }, [timeRange]);

  useEffect(() => {
    mountedRef.current = true;
    fetchData();

    let interval: NodeJS.Timeout | null = null;
    if (autoRefresh) {
      interval = setInterval(fetchData, 30000);
    }

    return () => {
      mountedRef.current = false;
      if (interval) clearInterval(interval);
    };
  }, [timeRange, autoRefresh, fetchData]);

  const fixStuckCases = async () => {
    if (!window.confirm('×”×× ×œ×‘×˜×œ ××ª ×›×œ ×”×ª×™×§×™× ×”×ª×§×•×¢×™×?')) return;
    
    try {
      // âœ… P0 FIX: Use centralized API utility
      const result = await apiPost('/api/admin/fix-stuck-cases', {}, { showErrorToast: false });
      toast.success(`âœ… ${result.fixed_count} ×ª×™×§×™× ×ª×•×§× ×• ×‘×”×¦×œ×—×”!`);
      fetchData();
    } catch (error) {
      logger.error('Error fixing stuck cases:', error);
      toast.error('×©×’×™××” ×‘×ª×™×§×•×Ÿ ×ª×™×§×™× ×ª×§×•×¢×™×');
    }
  };

  if (loading) {
    return (
      <div className="p-8 flex items-center justify-center">
        <div className="text-center">
          <RefreshCw className="w-12 h-12 text-blue-500 animate-spin mx-auto mb-4" />
          <p className="text-white text-lg">×˜×•×¢×Ÿ × ×ª×•× ×™ ×‘×§×¨×ª ××™×›×•×ª...</p>
        </div>
      </div>
    );
  }

  if (!data) {
    return (
      <div className="p-8">
        <div className="bg-red-500/10 border border-red-500/30 rounded-lg p-6 text-center">
          <AlertTriangle className="w-12 h-12 text-red-500 mx-auto mb-4" />
          <p className="text-white text-lg">××™×Ÿ × ×ª×•× ×™× ×œ×”×¦×’×”</p>
          <button
            onClick={fetchData}
            className="mt-4 px-6 py-2 bg-blue-500 hover:bg-blue-600 rounded-lg text-white"
          >
            × ×¡×” ×©×•×‘
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6" dir="rtl">
      <div className="max-w-7xl mx-auto">
      <div className="mb-6 flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-white mb-2">ğŸ¯ ×‘×§×¨×ª ××™×›×•×ª</h1>
          <p className="text-slate-400">××¢×§×‘ ×•×‘×§×¨×” ×¢×œ ××™×›×•×ª ×”× ×™×ª×•×—×™× ×‘××¢×¨×›×ª</p>
        </div>
        
        <div className="flex gap-3">
          <select
            value={timeRange}
            onChange={(e) => setTimeRange(e.target.value)}
            className="bg-slate-800 border border-slate-700 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="1h">×©×¢×” ××—×¨×•× ×”</option>
            <option value="6h">6 ×©×¢×•×ª</option>
            <option value="24h">24 ×©×¢×•×ª</option>
            <option value="7d">×©×‘×•×¢</option>
            <option value="30d">×—×•×“×©</option>
            <option value="all">×”×›×œ</option>
          </select>
          
          <label className="flex items-center gap-2 bg-slate-800 border border-slate-700 rounded-lg px-4 py-2 text-white cursor-pointer">
            <input
              type="checkbox"
              checked={autoRefresh}
              onChange={(e) => setAutoRefresh(e.target.checked)}
              className="w-4 h-4"
            />
            <span>×¨×¢× ×•×Ÿ ××•×˜×•××˜×™</span>
          </label>
          
          <button
            onClick={fetchData}
            className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded-lg flex items-center gap-2 transition-colors"
          >
            <RefreshCw className="w-4 h-4" />
            ×¨×¢× ×Ÿ
          </button>
        </div>
      </div>

      {data.alerts && data.alerts.length > 0 && (
        <div className="mb-6 space-y-2">
          {data.alerts.map((alert, idx) => (
            <div
              key={idx}
              className={`p-4 rounded-lg border-r-4 ${
                alert.severity === 'high'
                  ? 'bg-red-500/10 border-red-500'
                  : 'bg-yellow-500/10 border-yellow-500'
              }`}
            >
              <div className="flex items-start gap-3">
                <AlertTriangle className={`w-5 h-5 mt-0.5 ${
                  alert.severity === 'high' ? 'text-red-500' : 'text-yellow-500'
                }`} />
                <div className="flex-1">
                  <div className="font-bold text-white">{alert.message}</div>
                  <div className="text-sm text-slate-400 mt-1">ğŸ’¡ {alert.action}</div>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}

      <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
        <SummaryCard
          title="×¡×”×´×› ×ª×™×§×™×"
          value={data.summary.total_cases}
          icon={<Activity className="w-8 h-8" />}
          color="bg-blue-500/10"
          iconColor="text-blue-500"
        />
        <SummaryCard
          title="×”×•×©×œ××•"
          value={data.summary.completed}
          subtitle={`${data.summary.success_rate.toFixed(1)}%`}
          icon={<CheckCircle className="w-8 h-8" />}
          color="bg-green-500/10"
          iconColor="text-green-500"
        />
        <SummaryCard
          title="× ×›×©×œ×•"
          value={data.summary.failed}
          subtitle={`${data.summary.failure_rate.toFixed(1)}%`}
          icon={<XCircle className="w-8 h-8" />}
          color="bg-red-500/10"
          iconColor="text-red-500"
        />
        <SummaryCard
          title="×‘×¢×™×‘×•×“"
          value={data.summary.analyzing}
          icon={<Clock className="w-8 h-8" />}
          color="bg-yellow-500/10"
          iconColor="text-yellow-500"
        />
      </div>

      {data.stuck_cases && data.stuck_cases.length > 0 && (
        <div className="mb-6 p-4 bg-red-500/10 border border-red-500/30 rounded-lg">
          <div className="flex justify-between items-center mb-3">
            <h3 className="text-lg font-bold text-red-400 flex items-center gap-2">
              <AlertTriangle className="w-5 h-5" />
              ×ª×™×§×™× ×ª×§×•×¢×™× ({data.stuck_cases.length})
            </h3>
            <button
              onClick={fixStuckCases}
              className="bg-red-500 hover:bg-red-600 text-white px-4 py-2 rounded-lg transition-colors"
            >
              ğŸ”§ ×ª×§×Ÿ ×ª×™×§×™× ×ª×§×•×¢×™×
            </button>
          </div>
          <div className="space-y-2">
            {data.stuck_cases.map((stuck) => (
              <div
                key={stuck.case_id}
                className="flex justify-between items-center bg-slate-800/50 p-3 rounded-lg"
              >
                <span className="font-mono text-sm text-slate-400">
                  {stuck.case_id.substring(0, 16)}...
                </span>
                <span className="text-white">{stuck.title}</span>
                <span className="text-red-400 font-bold">
                  {stuck.stuck_minutes.toFixed(1)} ×“×§×•×ª
                </span>
              </div>
            ))}
          </div>
        </div>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
        <MetricCard title="â±ï¸ × ×™×ª×•×— ×–×× ×™×" icon={<Clock className="w-6 h-6" />}>
          <div className="space-y-3">
            <MetricRow
              label="×–××Ÿ ×××•×¦×¢"
              value={`${data.timing.avg_duration_minutes.toFixed(1)} ×“×§×•×ª`}
            />
            <MetricRow
              label="×—×¦×™×•×Ÿ"
              value={`${data.timing.median_duration_minutes.toFixed(1)} ×“×§×•×ª`}
            />
          </div>
          
          {data.timing.slowest_cases && data.timing.slowest_cases.length > 0 && (
            <>
              <h4 className="font-bold mt-4 mb-2 text-white">ğŸŒ ×”×ª×™×§×™× ×”××™×˜×™×™× ×‘×™×•×ª×¨:</h4>
              <div className="space-y-2">
                {data.timing.slowest_cases.slice(0, 3).map((c, idx) => (
                  <div key={idx} className="text-sm flex justify-between bg-slate-800/30 p-2 rounded">
                    <span className="font-mono text-slate-400">
                      {c.case_id.substring(0, 12)}...
                    </span>
                    <span className="font-bold text-orange-400">
                      {c.duration_minutes.toFixed(1)} ×“×§×•×ª
                    </span>
                  </div>
                ))}
              </div>
            </>
          )}
        </MetricCard>

        <MetricCard title="ğŸš¨ × ×™×ª×•×— ×›×™×©×œ×•× ×•×ª" icon={<XCircle className="w-6 h-6" />}>
          {data.failures.by_category && Object.keys(data.failures.by_category).length > 0 ? (
            <div className="space-y-2">
              {Object.entries(data.failures.by_category as Record<string, number>).map(([category, count]) => (
                <div key={category} className="flex justify-between items-center">
                  <span className="text-slate-300">{category}</span>
                  <span className="font-bold text-red-400">{count}</span>
                </div>
              ))}
            </div>
          ) : (
            <div className="text-center py-4 text-slate-400">
              âœ… ××™×Ÿ ×›×™×©×œ×•× ×•×ª!
            </div>
          )}
        </MetricCard>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
        <MetricCard title="ğŸ“ˆ ××™×›×•×ª ×ª×•×¦××•×ª" icon={<TrendingUp className="w-6 h-6" />}>
          <div className="space-y-3">
            <MetricRow
              label="×××•×¦×¢ ×˜×¢× ×•×ª ×œ×ª×™×§"
              value={data.quality.avg_claims_per_case.toFixed(1)}
            />
            <MetricRow
              label="×××•×¦×¢ ×¡×ª×™×¨×•×ª ×œ×ª×™×§"
              value={data.quality.avg_contradictions_per_case.toFixed(1)}
            />
            <MetricRow
              label="×ª×™×§×™× ×œ×œ× ×¡×ª×™×¨×•×ª"
              value={data.quality.cases_with_no_contradictions}
              warning={data.quality.cases_with_no_contradictions > 0}
            />
          </div>
        </MetricCard>

        <MetricCard title="ğŸ’° × ×™×ª×•×— ×¢×œ×•×™×•×ª" icon={<DollarSign className="w-6 h-6" />}>
          <div className="space-y-3">
            <MetricRow
              label="×¢×œ×•×ª ×›×•×œ×œ×ª"
              value={data.costs.total_cost}
            />
            <MetricRow
              label="×¢×œ×•×ª ×××•×¦×¢×ª ×œ×ª×™×§"
              value={data.costs.avg_cost_per_case}
            />
          </div>
        </MetricCard>
      </div>

      {data.insights && data.insights.length > 0 && (
        <MetricCard title="ğŸ’¡ ×ª×•×‘× ×•×ª ×•×”××œ×¦×•×ª" icon={<TrendingUp className="w-6 h-6" />}>
          <div className="space-y-3">
            {data.insights.map((insight, idx) => (
              <div key={idx} className="border-r-4 border-blue-500 pr-3 py-2">
                <div className="font-semibold text-white">{insight.message}</div>
                {insight.recommendation && (
                  <div className="text-sm text-slate-400 mt-1">
                    ğŸ’¡ {insight.recommendation}
                  </div>
                )}
              </div>
            ))}
          </div>
        </MetricCard>
      )}
      </div>
    </div>
  );
};

const SummaryCard = ({ title, value, subtitle, icon, color, iconColor }: {
  title: string;
  value: any;
  subtitle?: string;
  icon: React.ReactNode;
  color: string;
  iconColor: string;
}) => (
  <div className={`${color} border border-slate-700 p-6 rounded-lg`}>
    <div className={`${iconColor} mb-3`}>{icon}</div>
    <div className="text-3xl font-bold text-white mb-1">{value}</div>
    <div className="text-sm text-slate-400">{title}</div>
    {subtitle && <div className="text-lg font-semibold text-white mt-1">{subtitle}</div>}
  </div>
);

const MetricCard: React.FC<{
  title: string;
  icon: React.ReactNode;
  children?: React.ReactNode;
}> = ({ title, icon, children }) => (
  <div className="bg-slate-800/50 border border-slate-700 p-6 rounded-lg">
    <div className="flex items-center gap-2 mb-4">
      <div className="text-blue-400">{icon}</div>
      <h3 className="text-xl font-bold text-white">{title}</h3>
    </div>
    {children}
  </div>
);

const MetricRow = ({ label, value, warning }: {
  label: string;
  value: string;
  warning?: boolean;
}) => (
  <div className="flex justify-between items-center py-1">
    <span className="text-slate-400">{label}</span>
    <span className={`font-bold ${warning ? 'text-orange-400' : 'text-white'}`}>
      {value}
    </span>
  </div>
);

export default QualityControlDashboard;
